/* App's static wrapper */
.App {
  text-align: center;
}

#Grovv-Logo {
  pointer-events: none;
  width: auto;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.App-body {
  background-color: #5a5252;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  color: white;
}

#App-Header {
  position: fixed;
  overflow: hidden;
  top: 0;
  height: 100px;
  width: inherit;
  background-color: #317d92;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

#Footer-Btns {
  /* padding-top: 2px; */
  position: absolute;
  padding-bottom: 2px;
  left: 64px;
  top: 70px;
  display: flex;
  justify-content: flex-end;
}

.QuickTemp-Row{
  /* padding-top: 2px; */
  padding-bottom: 2px;
  display: flex;
  justify-content: flex-end;
}

#Home-Div {
  height: 100px;
  background-color: #317d92;
}

#App-Body-Content {
  display: flex;
  flex-direction: row;
  height: 100%;
  z-index: 0;
}

#App-Inner-Body {
  width: 100vw;
}

#Main-Left-Wrapper {
  display: flex;
  flex-direction: row;
  position: fixed;
  z-index: 100;
}

#Main-Left {
  display: flex;
  flex-direction: column;
  width: 240px;
  z-index: 100;
  top: 0;
  left: 0;
  background-color: rgb(31, 30, 30);
  overflow-x: hidden;
}

#Main-Left-Menu {
  display: flex;
  flex-direction: column;
  width: 240px;
  z-index: 1;
  background-color: #6c8a66;
  overflow-x: hidden;
}

#Menu-Handle {
  height: 80px;
  margin-top: 21px;
  z-index: 100;
  position: relative;
}

.Left-Menu-Btn {
  background: #496870;
  font-size: 18px;
  font-weight: bold;
  text-align: right;
  border: none;
  height: 67px;
  margin-bottom: 2px;
  flex: 1;
}

.Left-Menu-Btn:hover {
  background: #5c7a81;
}

.Left-Menu-Btn:active {
  background: #7899a0;
}


.Temp-Gauge-Btn {
  background: #91eebb;
  font-size: 18px;
  font-weight: bold;
  border: none;
  text-align: center;
  width: 80px;
  height: 24px;
  margin-left: 4px;
}

.Humid-Gauge-Btn {
  background: #91eebb;
  font-size: 18px;
  font-weight: bold;
  border: none;
  text-align: center;
  width: 80px;
  height: 24px;
  margin-left: 4px;
}

.Humid-Gauge-Btn:hover {
  background: #eaebb4;
}

.Grow-Area-Btn {
  background: #91b4bd;
  font-size: 16px;
  font-weight: bold;
  border: none;
  text-align: center;
  width: 80px;
  height: 24px;
  /* margin-left: 4px; */
}

.Grow-Area-Btn:hover {
  background: #eaebb4;
}

.Grow-Area-Btn:active {
  background: #a4ccd6;
}


#Profile-Btn {
  background: #91bda8;
  font-size: 18px;
  font-weight: bold;
  border: none;
  text-align: center;
  width: 80px;
  height: 24px;
  margin-left: 4px;
}

#Profile-Btn:active {
  background: #90bfe6;
}

#Profile-Btn:hover {
  background: #76ad65;
}

#Logout-Btn {
  background: #91bda8;
  font-size: 18px;
  font-weight: bold;
  border: none;
  text-align: center;
  width: 80px;
  height: 24px;
  margin-left: 4px;
}

#Logout-Btn:active {
  background: #90bfe6;
}

#Logout-Btn:hover {
  background: #76ad65;
}


#Main-Content {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

/* Website view  */
.Site-View {
  overflow: auto;
}

.Site-View-Update {
  overflow: auto;
  /* width: calc(100% - 240px); */
  height: 100%;
  position: fixed;
}



